body{
    margin: 0;
    padding: 0;;
}
.a{
    height: 50%;
    display: flex;
    align-items: center;
}
.a-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
}
.a-right{
    flex: 1;
}
.a-card.bg{
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: #333;
}
.a-card{
    width: 60%;
    height: 100%;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
}
.a-desc{
    font-size: 1.4em;
}
.a-img{
    width: 100%;
    height: 60vh;
    object-fit: cover;
}
@media screen and (max-width: 768px) {
    .a {
        flex-direction: column;
        text-align: center;
        margin-top: 50px;
    }

    .a-left {
        width: 100%;
    }
        .a-card {
            height: 30vh;
        }
    
        .a-card.bg,
        .a-award {
            display: none;
        }
    
        .a-right {
            padding: 20px;
        }
}
@media screen and (max-width: 480px) {
    .a {
        flex-direction: column;
        text-align: center;
        margin-top: 50px;
    }
    .a-left{
        width: 100%;
    }
    .a-card{
        height: 30vh;
    }
    .a-card.bg, .a-award{
        display: none;
    }
    .a-right{
        padding: 20px;
    }
}