.body{
    padding: 0;
    margin: 0;
}
.p{
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid rgb(243, 242, 242);
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}
.p-browser{
    height: 20px;
    background-color:  rgb(243, 242, 242);
    display: flex;
    align-items: center;
    position: sticky;
    z-index: 2;
}
.p-circle{
    width: 6px;
    height: 6px;
    margin: 3px;
    border-radius: 50%;
    background-color: white;
}
.p-img{
    width: 100%;
}
.p:hover .p-img{
 transition: all 10s ease;
 transform: translateY(-100%);
}
@media screen and (max-width: 921px) {
    .p {
        width: 40%;
    }
}
@media screen and (max-width: 660px) {
    .p {
        width: 40%;
        height: 30vh;
 }
}
@media screen and (max-width: 480px) {
    .p {
        width: 40%;
        height: 20vh;

    }
}